import Parcel from "single-spa-react/parcel";
import { mountRootParcel } from "single-spa";
import { FeeSummaryParcel } from "../common-components";
import CTA from "./CTA";

const feeItems = [
  { label: "   ", price: 200 },
  { label: "  ", price: 200 },
  { label: "     ", price: 300 },
  { label: "    ", price: 250 },
  { label: "  ", price: 400 },
];

const handlePayment = (sendEvent, feeItems, url) => {
  sendEvent({
    type: "SUBMIT_PAYMENT_INFO",
    feeItems: feeItems,
  });
  // alert("Payment successful!");
  window.open(url, "_blank");
};

export default function Bill({ sendEvent, state }) {
  return (
    <>
      <Parcel
        config={FeeSummaryParcel}
        mountParcel={mountRootParcel}
        items={feeItems}
        expiryDate="20/6/2025"
        paymentTime="19:55:00"
        onPayment={() =>
          handlePayment(sendEvent, feeItems, "https://google.com")
        }
      />
      <br />
    </>
  );
}
