import React, { useState } from "react";
import Parcel from "single-spa-react/parcel";
import { mountRootParcel } from "single-spa";
import { ConfigProvider, Form } from "antd";
import { FormInputParcel, FormSelectParcel } from "../shared-ui";
import { styles } from "../styles";

interface FormErrors {
  companyName?: string;
  companyType?: string;
  activityType?: string;
  commercialRegister?: string;
  capital?: string;
}

export default function FormEntry({ form, request, isReadonly = false }) {
  // Form state for step 1
  const [formData, setFormData] = useState({
    companyName: "",
    companyType: undefined as string | undefined,
    activityType: undefined as string | undefined,
    commercialRegister: "",
    capital: "",
  });
  const [formErrors, setFormErrors] = useState<FormErrors>({});
  const [formTouched, setFormTouched] = useState<Record<string, boolean>>({});

  // ==========================================
  // DATA - service-b controls the data
  // ==========================================

  // Company type options
  const companyTypeOptions = [
    { label: "   ", value: "llc" },
    { label: " ", value: "joint_stock" },
    { label: " ", value: "partnership" },
    { label: "  ", value: "limited_partnership" },
  ];

  // Activity type options
  const activityTypeOptions = [
    { label: " ", value: "general_trade" },
    { label: "", value: "manufacturing" },
    { label: "", value: "services" },
    { label: " ", value: "real_estate" },
    { label: " ", value: "it" },
  ];

  // Handle field change
  const handleFieldChange = (field: string, value: string | number) => {
    setFormData((prev) => ({ ...prev, [field]: value }));
    setFormTouched((prev) => ({ ...prev, [field]: true }));

    // Clear error when user starts typing
    if (formErrors[field as keyof FormErrors]) {
      setFormErrors((prev) => ({ ...prev, [field]: undefined }));
    }
  };

  console.warn(request?.machineSnapshot?.context?.formData);

  return (
    <>
      <ConfigProvider
        theme={{
          token: {
            colorTextDisabled: "rgba(0,0,0,0.5)",
          },
        }}
      >
        <div style={styles.formContainer}>
          <Form
            disabled={isReadonly}
            initialValues={{
              ...request?.machineSnapshot?.context?.formData,
            }}
            form={form}
            name="formEntry"
            layout="vertical"
          >
            <Form.Item
              name={["formEntry", "companyName"]}
              label=" "
              rules={
                isReadonly
                  ? []
                  : [
                      {
                        required: true,
                        message: "  ",
                      },
                      {
                        min: 3,
                        message: "    3   ",
                      },
                    ]
              }
            >
              <Parcel
                config={FormInputParcel}
                mountParcel={mountRootParcel}
                disabled={isReadonly}
                onChange={(value: string) =>
                  handleFieldChange("companyName", value)
                }
                placeholder="  "
              />
            </Form.Item>

            <Form.Item
              label=" "
              name={["formEntry", "companyType"]}
              rules={
                isReadonly
                  ? []
                  : [
                      {
                        required: true,
                        message: "  ",
                      },
                    ]
              }
            >
              <Parcel
                config={FormSelectParcel}
                mountParcel={mountRootParcel}
                value={formData.companyType}
                onChange={(value: string) =>
                  handleFieldChange("companyType", value)
                }
                options={companyTypeOptions}
                placeholder="  "
                error={
                  formTouched.companyType ? formErrors.companyType : undefined
                }
                disabled={isReadonly}
              />
            </Form.Item>

            <Form.Item
              name={["formEntry", "activityType"]}
              label=" "
              rules={
                isReadonly
                  ? []
                  : [
                      {
                        required: true,
                        message: "  ",
                      },
                    ]
              }
            >
              <Parcel
                config={FormSelectParcel}
                mountParcel={mountRootParcel}
                value={formData.activityType}
                placeholder="  "
                onChange={(value: string) =>
                  handleFieldChange("activityType", value)
                }
                options={activityTypeOptions}
                error={
                  formTouched.activityType ? formErrors.activityType : undefined
                }
                disabled={isReadonly}
              />
            </Form.Item>

            <Form.Item
              name={["formEntry", "commercialRegister"]}
              label="  "
              rules={
                isReadonly
                  ? []
                  : [
                      {
                        required: true,
                        message: "   ",
                      },
                      {
                        validator: (_, value) => {
                          if (!value) return Promise.resolve();

                          if (isNaN(Number(value))) {
                            return Promise.reject("   ");
                          }

                          return Promise.resolve();
                        },
                      },
                    ]
              }
            >
              <Parcel
                config={FormInputParcel}
                mountParcel={mountRootParcel}
                placeholder="  "
                value={formData.commercialRegister}
                onChange={(value: string) =>
                  handleFieldChange("commercialRegister", value)
                }
                error={
                  formTouched.commercialRegister
                    ? formErrors.commercialRegister
                    : undefined
                }
                disabled={isReadonly}
              />
            </Form.Item>

            <Form.Item
              name={["formEntry", "capital"]}
              label=" "
              rules={
                isReadonly
                  ? []
                  : [
                      { required: true, message: "  " },
                      {
                        validator: (_, value) => {
                          if (!value) return Promise.resolve();

                          if (isNaN(Number(value))) {
                            return Promise.reject("   ");
                          }

                          return Promise.resolve();
                        },
                      },
                    ]
              }
            >
              <Parcel
                config={FormInputParcel}
                mountParcel={mountRootParcel}
                placeholder="  "
                value={formData.capital}
                onChange={(value: string) =>
                  handleFieldChange("capital", value)
                }
                error={formTouched.capital ? formErrors.capital : undefined}
                disabled={isReadonly}
              />
            </Form.Item>
          </Form>
        </div>
      </ConfigProvider>
    </>
  );
}
